import { Component, OnInit, OnDestroy, ChangeDetectorRef, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import { IntervensionService } from "../intervension.service";

import { environment } from '../../../environments/environment';
import { BaseForm } from '../../shared/base-form';
import { ToastrService } from 'ngx-toastr';

@Component({
  	selector: 'app-form',
  	providers: [],
  	templateUrl: './form.component.html',
  	styleUrls: ['./form.component.css']
})

export class FormComponent extends BaseForm  implements OnInit {
    public language;
    
    public input = [];
    public minimum = 1;
    public maximum = 54;
    public elementTypes = ["Text", "Radio"] ;
  	constructor(
	    private router: Router,
	    private activatedRoute: ActivatedRoute,
      private _cd: ChangeDetectorRef,
	    private klassService: IntervensionService,
	    private toastr: ToastrService
  	) { super(); }

  	ngOnInit() {
	    this.item.is_active = true ;
	    this.item.contents = [];
	    this.language = 'en';
	    // subscribe to router event
	    this.activatedRoute.params.subscribe((params: Params) => {
	        this.itemID = params['id'];
	        if(this.itemID){
	          this.isEditable = true;
	          this.getItem();
	        }
	    });

      this.interventionsList()
	
    }

  	getItem() {
     	this.busy = this.klassService.getItem(this.itemID, this.language).then((res) => {
      		if(!res)
        		return true;

      		this.item = res.data;
      });
  	}

  	createItem() {
    	this.busy = this.klassService.addItem(this.item, this.language, 'intervention').then((res) => {
      		if(!res)
        		return true;
          this.toastr.success(' ','Intervention Created.', { timeOut: 3000, });
          this.item = res;
      		this.router.navigate(['/intervention']);
    	});
  	}


  	updateItem() {
	    this.busy = this.klassService.updateItem(this.itemID, this.item, this.language, 'intervention').then((res) => {
	      	if(!res)
	        	return true;
          this.toastr.success(' ','Intervention Updated.', { timeOut: 3000, });
          this.router.navigate(['/intervention']);
	    });
  	}

    interventionsList(){

      for(var i=this.minimum;i<=this.maximum;i++){
        this.input.push(i);
      }
      return this.input
    }

    addOption(i,j){

        let newoption = {
                displayText:"",
                answerValue:""
        } ;
        this.item.contents[i].questions[j].answerKeys.push(newoption) ;

      }
      removeOption(i,j,k){
          this.item.contents[i].questions[j].answerKeys.splice(k,1) ;
      }

      addContent() {
        let newoption = {
            content:'',
            questions:[{
              question:'',
              elementType:'Text',
              answerKeys:[]
            }]
          } ;
        this.item.contents.push(newoption);
      }

      addQuestion(i) {
        let newoption ={
              question:'',
              elementType:'Text',
              answerKeys:[]
        }
        this.item.contents[i].questions.push(newoption)
      }

}


