import { NgModule } from '@angular/core';

import { AppSharedModule } from '../shared/app-shared.module';

import { IntervensionRoutingModule } from './intervension.routing';

import { IntervensionComponent } from './intervension.component';
import { FormComponent } from './form/form.component';
import { ListingComponent } from './listing/listing.component';
import { IntervensionService } from "./intervension.service";
import { ImageUploadModule } from 'ng2-imageupload';
@NgModule({
  imports: [
    AppSharedModule,
    IntervensionRoutingModule,
    ImageUploadModule
  ],
  providers: [IntervensionService],
  declarations: [IntervensionComponent, FormComponent, ListingComponent]
})
export class IntervensionModule { }



